import React, { Component } from "react";

class Resume extends Component {
  render() {
    if (this.props.data) {
      var skillmessage = this.props.data.skillmessage;
      var unipic = "images/" + this.props.data.image;
      var workpics = this.props.data.workimage;

      //console.log(this.props.data.education);
      var education = this.props.data.education.map(function (education) {
        return (
          <div key={education.school}>
            <div className="three columns">
              <img className="uni-pic" src={unipic} alt="UOfA Pic" />

              <h3 style={{ textAlign: "center" }}>{education.school}</h3>
            </div>
            <p className="info">
              {education.degree}
              <br />
              <p>
                Graduating: <em className="date">{education.graduated}</em>
              </p>
            </p>
            <p>
              {" "}
              GPA: {education.description.gpa} <br />
              {education.description.position1}
              <br />
              {education.description.position2}
            </p>
          </div>
        );
      });
      var work = this.props.data.work.map(function (work) {
        return (
          <div key={work.compkey}>
            <div className="three columns">
              <img
                className="uni-pic"
                src={"/images/" + workpics[work.compkey]}
                alt="company logo"
              />

              <h3 style={{ textAlign: "center" }}>{education.school}</h3>
            </div>
            <h3>{work.company}</h3>
            <p className="info">
              {work.title}
              <span>&bull;</span> <em className="date">{work.years}</em>
            </p>
            <p>{work.gpa}</p>
            <div className="work-gap"></div>
          </div>
        );
      });
      var skills = this.props.data.skills.map(function (skills) {
        var className = "bar-expand " + skills.name.toLowerCase();
        return (
          <li key={skills.name}>
            <span style={{ width: skills.level }} className={className}></span>
            <em>{skills.name}</em>
          </li>
        );
      });
    }

    return (
      <section id="resume">
        <div className="row education">
          <div className="three columns header-col">
            <h1>
              <span>Education</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">{education}</div>
            </div>
          </div>
        </div>

        <div className="row work">
          <div className="three columns header-col">
            <h1>
              <span>Work</span>
            </h1>
          </div>

          <div className="nine columns main-col">{work}</div>
        </div>

        <div className="row skill">
          <div className="three columns header-col">
            <h1>
              <span>Skills</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <p>{skillmessage}</p>

            <div className="bars">
              <ul className="skills">{skills}</ul>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Resume;
